.mainContentContainer{
    width: 100%;

    display: flex;
    flex-direction: column;

    z-index: 10000;
}

.navigationBarContainer {
    width: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;

    background: #2a2a2a;
}

.rightLinksContainer {
    margin-left: auto;

    padding: 50px 10% 0 0;

    display: flex;
    flex-direction: row;
    align-items: center;
}

.linkText {
    font-size: 1.3em;
    font-weight: 600;

    text-decoration: none;
    color: rgba(255, 255, 255, 0.8);

    margin: 0 25px;

    font-family: 'Source Sans Pro', sans-serif;

    cursor: pointer;
}

.rightLinksContainer a::after, .rightLinksContainer p::after {
    content: '';
    display: block;

    border-bottom: 3px solid #525252;

    width: 0;
    position: absolute;
    right: 0;

    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;

    padding: 0 0 20px 0;
}
  
.rightLinksContainer a:hover::after, .rightLinksContainer p:hover::after { 
    width: 100%; 
}


/* Hover content (appears below links) */
.hoverContent {
    width: 100%;

    background: red;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    margin: 23px 0 0 0;

    transform: scaleY(0.5);
    transform-origin: 100% 50%;
    transition: ease-in-out 0.5s;
}

.innerContent {
    z-index: 5;

    width: 100%;

    background: rgba(0, 0, 0, 0.3);

    position: absolute;
    top: 0;
}